.vertical-line {
  width: 3px;
  height: 9em;
  background:red;
}

.vertical .stepper {
  -webkit-flex-direction: column;
          flex-direction: column;
}
.vertical .step {
  position: relative;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  /* width: 100%;
  min-height: 9em; */
}
.vertical .step:after {
  display: inline;
  width: 3px;
  position: absolute;
  height: 100%;
  left: 0.85em;
  top: 0.5em;
  z-index: -1;
}
.todo {
  float:left;
  width:50px;
}
.vertical .step:before {
  display: none;
}
.vertical .step:last-child:after {
  display: none;
}
.vertical .step-content {
  margin-left: 1em;
  margin-top: -0.5em;
  color: #3c4859;
}

